import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardActions, CardTitle, CardText, Button, CardMenu, IconButton} from 'react-mdl';

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {activeTab : 0};
    }

    toggleCategories = () => {
        if(this.state.activeTab === 0) {
            return(
                <div className="projects-grid">
                    {/*Project 1*/}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,) center  / cover'}}>
                            C++ Project #1
                        </CardTitle>
                        <CardText>
                            sdpofdspgmadffgdfgdfgs
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>
                                        {/*Project 1*/}
                                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,) center  / cover'}}>
                            C++ Project #2
                        </CardTitle>
                        <CardText>
                            sdpofdspgmadffgdfgdfgs
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>
                </div>
            )
        } else if(this.state.activeTab === 1) {
            return(
                <div><h1>This is Java</h1></div>
            )
        } else if(this.state.activeTab === 2) {
            return(
                <div><h1>This is ML/AI</h1></div>
            )
        } else if(this.state.activeTab === 3) {
            return(
                <div><h1>This is Web</h1></div>
            )
        }
    }

    render() {
        return(
            <div className="category-tabs">
                <Tabs activeTab={this.state.activate} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>C++</Tab>
                    <Tab>Java</Tab>
                    <Tab>ML/AI</Tab>
                    <Tab>Web</Tab>
                </Tabs>
                <Grid>
                    <Cell col={12}>
                        {this.toggleCategories()}
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;